import { Component } from '@angular/core';
import { IonicPage, NavController} from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../providers';
import { Http } from '@angular/http';

import 'rxjs/add/operator/map'

@IonicPage()
@Component({
  selector: 'page-treeview',
  templateUrl: 'treeview.html',
})
export class TreeviewPage {
  information: any=[];
  items: any=[];
 
  constructor( public http: Http,public msj: NotificationProvider,public navCtrl: NavController ,public regService: Auth) {

    this.pideregistros();

  }

  pideregistros() { 



    let localData = this.http.get('assets/data/information.json').map(res => res.json().items);
    localData.subscribe(data => {
      this.information = data;
    })

   /*
    this.regService.getRegtree().then((data) => {
      this.information=data;
     
}, (err) => {
  this.msj.showError3('Error',err.error);
 // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })  
});
*/
/*
    let localData = http2.get('./providers/data/information.json').map(res => res.json().items);
    localData.subscribe(data => {
      this.information = data;
    })
    */
  }
 
  toggleSection(i) {
    this.information[i].open = !this.information[i].open;
  }
 
  toggleItem(i, j) {
    this.information[i].children[j].open = !this.information[i].children[j].open;
  }

}



